import S from "./Scoring.styled";

const Scoring = ({grading}) => {

    return (
        <S.Scoring>
            {grading.message ? 
                <S.Div>
                    <S.P>{grading.message}</S.P> 
                    <S.P>
                        {grading.failCount === 0 ? 
                            "   .\n!" 
                            : `${grading.failCount}   .`}
                    </S.P> 
                </S.Div>
                : <S.P>  ,   .</S.P>}
        </S.Scoring>
    );
}

export default Scoring;