import S from "./AccountInfo.styled";
import C from "../../CommonStyled";
import { useSelector, useDispatch } from "react-redux";
import { useState } from "react";
import { changeNickName } from "./../../../redux/reducer/accountSlice";
import { showNotification } from "../../../redux/action";
import Login from "../../Login";

const AccountInfo = () => {
    const state = useSelector(state => state.account);
    const dispatch = useDispatch();
    const [newNickname, setNewNickname] = useState("");

    const handleNickname = () => {
        if(newNickname.length >= 4){
            dispatch(changeNickName(newNickname));
        } else {
            dispatch(showNotification(" 4   ."));
        }
    }

    return (
        <S.AccountInfo>
            {state?.account ? 
            <>
                <S.H1>  : {state.account}</S.H1>
                <S.H1> : {state.nickname}</S.H1>
                <S.Div>
                    <S.Label> </S.Label>
                    <S.Input placeholder=" " onChange={(e) => setNewNickname(e.target.value)} />
                    <C.Button onClick={handleNickname}></C.Button>
                </S.Div> 
            </> : <Login/>}
        </S.AccountInfo>
    );
}

export default AccountInfo;