import S from "./Argument.styled";

const Argument = ({index, handleArgTypes}) => {
    return (
        <S.Argument>
            {`${index + 1}  `}
            <S.Select name="type" onChange={(e) => handleArgTypes(index, e.target.value)}>
                <S.Option value="string"></S.Option>
                <S.Option value="number"></S.Option>
                <S.Option value="boolean"></S.Option>
                <S.Option value="object"></S.Option>
                <S.Option value="array"></S.Option>
            </S.Select>
        </S.Argument>
    );
}

export default Argument;